.resume{
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    border: 2px solid black;

}

.resume-header{
    display: flex;
    width: 100%;
    height: 30vh;
    border-bottom:2px solid  black ;

    /* background-color: rgb(43, 175, 109); */
}

.header-left{
/* background-color: rgb(10, 154, 159); */
width: 70%;
padding: 2%;
margin-left: 2rem;
}
.my-name{
    font-size: 4rem;
}
.map{
    margin-top: 1rem;
}
.header-left p{
    justify-content: space-between;
}

.header-right{
width: 30%;
padding: 3%;

}
.header-right img{
width: 60%;
transform: scale(0.8);
border: 2px solid green;
border-radius:300px 300px ;
margin-top: -3rem;
float: right;
margin-right: 10rem;
}
.social-icons {
display: flex;
flex-direction: column;
margin-left: -3rem;
 margin-top: -8rem;
}

.social-icons a{
    text-decoration: none;
    color: rgb(153, 0, 255);
    
}

h3{
    color: rgb(153, 0, 255);

}


/*__________________Second Header______________________________*/

.second-header{
    display: flex;
    border-bottom:2px solid ;

}
.education{
    width:50%;
    display: block;
    padding: 2%;
}

.college{
 font-size: 1.7rem;   
 font-weight: 600;
 margin-top: 1rem;
}

.course-year{
    margin-left: 13rem;
    font-size: 1.3rem;
    color: rgb(131, 124, 124);
}

.btech p{
    font-size: 14px;
}

.school-name{
font-size: 1.7rem;   
 font-weight: 600;
 margin-top: 1rem;
}
.school{
    margin-left: 4rem;
    font-size: 1.3rem;
    color: rgb(131, 124, 124);
}
.school-tenth{

    margin-left: 17rem;
    font-size: 1.3rem;
    color: rgb(131, 124, 124);

}
.skills{
    width:50%;
    display: block;
    padding: 2%;
    border-left:2px solid  ;

}
.skill-body{
    margin-top:3rem;
    align-items: center;
}
.skill-body .col-2{
    margin-top: 1rem;
}
/*__________________Internships______________________________*/
.internship{
    width:100%;
    padding: 3%;
    border-bottom:2px solid ;
}

.internship-header{
    text-align: center;
}

.internship-company{
 font-size: 1.7rem;   
 font-weight: 600;
 margin-top: 1rem;
}
.internship-year{
float: right;
font-size: 1.3rem;
color: rgb(131, 124, 124);
}

.internship-role{
    font-size: 1.2rem;
    color: blue;

}

.works{
    margin-top: 2rem;
    width: 80%;
    margin-left: 4rem;
}

/*__________________Projects______________________________*/

.projects{
    width:100%;
    padding: 3%;
    border-bottom:2px solid ;
}

.project-1-title{
    margin-top: 1rem;
    font-size: 1.5rem;
    font-weight: 600;
    display: flex;
    float: inline-start;
}
